.container {
  width: 1240px;
  margin-left: auto;
  margin-right: auto;
}

.row {
  display: flex;
  justify-content: space-between;
  flex-flow: row wrap;
}

.align-items-center {
  align-items: center;
}

.col-2 {
  width: 50%;
  box-sizing: border-box;
}

.col-3 {
  width: 33.333333%;
  box-sizing: border-box;
}

.col-4 {
  width: 25%;
  box-sizing: border-box;
}

html, body {
  font-family: 'Montserrat', sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 1.4;
  color: #323F4C;
}

.h1 {
  font-size: 40px;
}

.h2 {
  font-size: 32px;
}

.h3 {
  font-size: 18px;
}

.symbol-icon {
  display: block;
  fill: #A3A3A3;
  transition: 0.2s;
}

.symbol-icon:hover {
  fill: #323F4C;
}

.symbol-logo {
  width: 144px;
  height: 31px;
}

.symbol-arrow-down {
  width: 24px;
  height: 24px;
}

.symbol-flag-ru, .symbol-flag-en, .symbol-flag-jp, .symbol-flag-zh, .symbol-flag-es {
  background-image: url(/assets/images/languages.png);
  background-position: 0px 0px;
  background-size: 174px 22px;
  background-repeat: no-repeat;
  width: 34px;
  height: 22px;
  box-shadow: 0 0 1px #999;
}

.symbol-flag-en {
  background-position: -35px 0;
}

.symbol-flag-jp {
  background-position: -70px 0;
}

.symbol-flag-zh {
  background-position: -105px 0;
}

.symbol-flag-es {
  background-position: -140px 0;
}

.button {
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  padding: 14px 36px;
  border-radius: 50px;
  text-decoration: none;
  transition: 0.2s;
  letter-spacing: 0.5px;
}

.button:hover {
  opacity: .8;
  text-decoration: none;
}

.button-primary {
  color: #fff;
  background: #fee500;
  background: -moz-linear-gradient(left, #fee500 0%, #ffb533 100%);
  background: -webkit-linear-gradient(left, #fee500 0%, #ffb533 100%);
  background: linear-gradient(to right, #fee500 0%, #ffb533 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fee500', endColorstr='#ffb533',GradientType=1 );
}

.header {
  padding: 40px 0 80px;
}

.nav {
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav__item {
  display: inline-block;
}

.nav__item a {
  color: #323F4C;
  text-decoration: none;
  padding: 10px 20px;
}

.lang-item__flag {
  display: inline-block;
  margin: 0 4px;
}

.footer {
  padding-top: 120px;
  padding-bottom: 120px;
}

.we-best {
  padding-top: 90px;
  padding-bottom: 140px;
}

.we-best__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 300px;
  font-size: 18px;
}

.we-best__icon {
  margin-right: 15px;
}

.we-best .col-3:nth-child(2) .we-best__item {
  max-width: 300px;
  padding: 15px 40px;
  box-shadow: 0 0 80px rgba(255, 181, 51, 0.2);
  border-radius: 50px;
  font-size: 14px;
  margin-left: auto;
  margin-right: auto;
}

.we-best .col-3:nth-child(3) .we-best__item {
  float: right;
}

.about__title {
  max-width: 75%;
}

.about .h2 {
  margin-bottom: 60px;
}

.about__description {
  padding-left: 75px;
}

.whitepaper {
  padding-top: 130px;
  padding-bottom: 110px;
}

.whitepaper__title {
  margin-bottom: 60px;
}

.whitepaper .download {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 60px;
}

.whitepaper .download__item {
  border: 2px solid #FCD133;
  border-radius: 30px;
  width: 100px;
  text-align: center;
  color: #323F4C;
  text-decoration: none;
  padding: 12px 15px 12px 40px;
  position: relative;
}

.whitepaper .download__item:hover .download__button {
  border: none;
  padding: 11px;
  background: #ffb533;
  background: -moz-linear-gradient(left, #ffb533 0%, #fee500 100%);
  background: -webkit-linear-gradient(left, #ffb533 0%, #fee500 100%);
  background: linear-gradient(to right, #ffb533 0%, #fee500 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffb533', endColorstr='#fee500',GradientType=1 );
}

.whitepaper .download__item:hover .symbol-icon {
  fill: #fff;
}

.whitepaper .download__button {
  position: absolute;
  z-index: 2;
  width: 47px;
  height: 47px;
  left: -2px;
  top: -2px;
  border: 2px solid #FCD133;
  border-radius: 50%;
  padding: 9px;
  box-sizing: border-box;
}

.whitepaper .download__button .symbol-icon {
  fill: #FCD133;
}



